@import url(https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap);
@import url(https://fonts.googleapis.com/css?family=Antic&display=swap);
@import url(https://fonts.googleapis.com/css?family=Antic|Lobster&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.home-page { 
    min-height: 100vh;
    min-width: 100vw;
}

.intro-heading {
    float: right;
    font-size: 2.1em;
}

.icon {
    border-radius: 50%;    
    height: 80px;
    margin-top: 15px;
    opacity: 0.8;
}


.nav-bar {
    display: flex;
    float: right;
    flex-direction: column;
    justify-content: space-around;
    margin: 15px;
    position: fixed;
    right: 0;
    text-align: center;
    top: 100px;
}

.main-pic {
    border-radius: 15px;
    float: left;
    margin: 5px;
    width: 75%;
  }

.nav-link {
    font-size: 1.2em;
}

a {
    color: white;
    display: flex;
    flex-direction: column;
    font-family: 'Roboto Mono', monospace;
    -webkit-transition: smooth;
    transition: smooth;
}

a:visted {
    color: teal;
}

a:hover {
    color: teal;
}
