.about-section {
    display: flex;
    /* flex-direction: column; */
    justify-content: flex-end;
    margin-top: 0px;
    padding: 10px;
    position: -webkit-sticky;
    position: sticky;
}

.about-section-text {
    background-color: #cce0e0;
    border-radius: 15px;
    font-family: 'Antic', sans-serif;
    font-size: 1.25em;
    height: 300px;
    margin: 15px;
    opacity: 0.8;
    padding: 15px;
    text-align: left;
    width: 800px;
}

h4 {
    color: #697f7f;
    font-family: 'Lobster', cursive;
    font-size: 1.3em;
    margin-bottom: 2px;
}

.stuff-i-like {
    background-color: #cce0e0;
    border-radius: 15px;
    font-family: 'Antic', sans-serif;
    font-size: 1em;
    height: 250px;
    margin: 15px;
    opacity: 0.8;
    overflow-y: scroll;
    padding: 16px;
    text-decoration: none;
    width: 180px;
}

li {
    list-style-type: none;
}

.headshot {
    border-radius: 15px;
    height: 500px;
    margin: 15px;
}



.skills {
    min-height: 80vh;
    width: 90%;
    overflow: hidden;
    padding: 0px;
}

.skills-logo {
    background: transparent;
    height: 300px;
    margin: 15px;
}

div.alice-carousel__play-btn-wrapper {
    background-color: transparent;
}

p.alice-carousel__next-btn-item,
p.alice-carousel__prev-btn-item {
    /* background-color: #C8E4E7; */
    /* border-radius: 15px; */
    color: #697f7f;
    font-family: 'Antic', sans-serif;
    font-size: 1.5em;
    font-weight: bold;
    /* padding: 8px; */
}

p.alice-carousel__next-btn-item:hover,
p.alice-carousel__prev-btn-item:hover {
    color: white;
}

.projects-content {
    width: 90%;
}

.project-display {
    display: flex;
    justify-content: space-around;
}

.project-title {
    color:  #cce0e0;
    font-family: 'Lobster', cursive;
    font-size: 1.5em;
    margin-top: 0px;
    margin-right: 15px;
}

.project-description {
    background-color: #697f7f;
    border-radius: 15px;
    font-family: 'Antic', sans-serif;
    font-size: 1.3em;
    margin: 15px;
    opacity: 0.7;
    padding: 15px;
    width: 500px;
}

.project-image {
    border-radius: 15px;
    height: 400px;
}

.tech-list.li {
    font-weight: bold;
}


.contact-info {
    background-color: #cce0e0;
    border-radius: 15px;
    float: right;
    font-family: 'Antic', sans-serif;
    font-size: 1.3em;
    margin: 15px;
    opacity: 0.8;
    padding: 30px;
    text-align: left;
    width: 600px;
}

h3 {
    margin: 10px;
}
* {
  /* border-radius: 15px; */
  box-sizing: border-box;
  padding: 0;
  margin: 0px;
  scroll-behavior: smooth;
}

h1, 
h2 {
  color: #697f7f;
  font-family: 'Lobster', cursive;
  font-size: 3em;
  margin-top: 0px;
  margin-right: 15px;
}

h2 {
  font-size: 2.5em;
  margin-top: 15px;
}

.App {
  background-color: #C8E4E7;
  display: flex;
  flex-direction: column;
  min-width: 100vw;
  text-align: right;
}


